.main-userprofile-container {
    width: 81%;
    height: 100vh;
    background: linear-gradient(
      30deg,
      rgb(43, 26, 22),
      chocolate,
      rgb(29, 202, 29),
      rgb(139, 193, 13),
      rgb(37, 2, 67)
    );
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main-userprofile-box {
    width: 95%;
    height: 90%;
    background: linear-gradient(
      300deg,
      rgb(43, 26, 22),
      chocolate,
      rgb(139, 193, 13),
      rgb(37, 2, 67)
    );
    border-radius: 20px;
  }
  .main-userprofile-heading-box {
    margin-top: 10px;
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgb(205, 205, 205);
  }
  .main-userprofile-heading-text {
    color: rgb(255, 255, 255);
  }
  .main-userprofile-qr-box{
    width: 100%;
    height:550px ;
    display: flex;
    align-items: center;
    justify-content: center;

  }
  .qrbackground{
    width: 400px;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    background-color: white;
  }