.main-product-buy-container {
  margin-top: 60px;
  width: 100%;
  height: 91.5vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-product-container {
  width: 40%;
  height: 90%;
  background-color: white;
  border-radius: 10px;
  border: 1px solid rgb(194, 194, 194);
}
.payment-methods-heading {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(225, 225, 225);
}
.showproduct-details {
  margin-top: 10px;
  margin-left: 40px;
  width: 90%;
  padding: 10px;
  height: auto;
}
.productamount-container {
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
}

.paymentmethod-show{
    margin-left: 40px;
    width: 80%;
    height: 200px;
}
.showproduct-orderplaced-details{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Button-order-placed{
    padding: 15px;
    width: 200px;
    border:  1px solid rgb(199, 199, 199);
    border-radius: 10px;
    background-color: rgba(5, 115, 217, 0.914);
    color: white;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}



.product-name-show{
    font-size: 14px;
    font-weight: 700;
    margin-right: 5px;
    color: rgb(0, 0, 0);
}

.product-amount-value{
    font-size: 14px;
    font-weight: 600;
    color: rgb(43, 43, 43);
}
.product-name{
    width: 400px;
    height: 20px;
    overflow: hidden;
}

.online{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: flex-end;
}
.cash-on-delevery{
    width: 100%;
    height: 30px;
    display: flex;
    margin-top: 10px;
    align-items: flex-start;
}
.address-container-payment{
  width: 100%;
  height: auto;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.deliveryaddress-name{
  font-size: 16px;
  font-weight: 700;
}