.Main-cart-container {
  margin-top: 60px;
  width: 100%;
  height: 91.5vh;
  display: flex;
  align-items: center;
}
.maincart-list-container{
    top: 60px;
    left: 200px;
    position: fixed;
    width: 700px;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: white;
    border:1px  solid rgb(202, 202, 202);
}
.maincart-list-box{
    margin-bottom: 70px;
    width: 100%;
    height: auto;
}
.maincart-list-amount-show-container{
    top: 120px;
    right: 100px;
    position: fixed;
    width: 400px;
    height: 350px;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: white;
    border-radius: 10px;
    border:1px  solid rgb(183, 183, 183);
}
.button-amount-container{
    
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.button-box-con{
    padding: 10px;
    width: 150px;
    background-color: rgb(255, 100, 129);
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 800;
    border-radius: 10px;
    cursor: pointer;
    border: 1px solid rgb(255, 200, 200);
}

.cartbox-container{
    width: 98%;
    height:auto;
    background-color: rgb(214, 220, 245);
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: default;
}
.titlebox-cart-container{
    width:73%;
    height: 30px;
    display: flex;


}
.image-cart-container{
    width: 150px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5px;
    margin-right: 10px;
    
}
.side-container-cart-box{
    width: 60%;
    height: 90%;
    border-left: 1px solid black;
    padding: 0px 10px;
}
.deleteicon{
    width: 80px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content:center;
    cursor: pointer;
    color: crimson;
    font-weight: bolder;
}