.main-class-container {
  margin-top: 60px;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-class-box-container {
  width: 100%;
  height: 100%;
  background: linear-gradient(
    20deg,
    rgb(176, 37, 37),
    rgb(17, 77, 17),
    chocolate,
    rgb(130, 20, 42),
    coral
  );
  display: flex;
  align-items: center;
}
.class-box-left-container {
  width: 300px;
  height: 100vh;
  background: linear-gradient(
    200deg,
    rgba(17, 174, 77, 0.718),
    rgb(26, 101, 101),
    green
  );
  display: flex;
  align-items: center;
  justify-content: center;
}
.class-box-container {
  width: 90%;
  height: 90%;
}
.class-box-userdetails {
  width: 100%;
  height: 50px;
  display: flex;
  border-radius: 10px;
  cursor: pointer;
  border: 2px solid rgb(255, 255, 255);
  transition: 1s;
  margin-bottom: 15px;
}
.class-link-userdetails{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    border-radius: 10px;
    cursor: pointer;
    color: rgb(246, 255, 0);
    transition: 1s;
    margin-bottom: 15px;
    text-decoration: none;
}
.class-link-userdetails:hover {
  background-color: rgb(255, 242, 233);
  color: darkorange;
  border: none;
  transition: 2s;
}
.topbox {
  margin-top: 20px;
}

.deletprofile{
    color: rgb(255, 0, 0);
}
 
.icon-details{
margin-right: 5px;
margin-left: 20px;
}