.offlinepage-main-container{
    margin-top: 60px;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}
.offline-img-container{
    width: 100%;
    height: 100%;
  
}
.offline-img{
    width: 400px;
    height:300px;
}
.offline-text-container{
    width: 100%;
    height: auto;
}
.offline-text-h1{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.offline-text-h2{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.offline-refresh-button-container{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.refreshbutton{
    padding: 13px;
    width: 150px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: blueviolet;
    color: white;
    cursor: pointer;
    font-size: 14px;
}
.offline-img-box-container{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}