.main-home-product-lisining{
    margin-top: 60px;
    width: 100%;
    height: 91vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.filter-sidebar-container{
    width: 280px;
    height: 91vh;
    position: fixed;
    top: 60px;
    left: 0px;
    background-color: white;
    border-right: 1px solid rgb(207, 207, 207);
}
.allhome-details-list-container{
    width: 80%;
    height:91vh;
    margin-left: 280px;
    overflow-y:scroll;
   
  }
  .allproject-details-box-container {
    margin-top: 10px;
    width: 100%;
    height:auto;
  
  }
  .allproject-details-scroll {
  
    width: 100%;
    height: auto;
   
  
  }
  .allproject-home-details-box {
    width: 260px;
    height:300px;
    float:left;
    background-color: rgb(255, 255, 255);
    margin: 20px;
    border-radius: 10px;
    border: 1px solid rgb(215, 215, 215);
    cursor: pointer;
    text-decoration: none;
  }
  .product-image-box-container {
    width: 100%;
    height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .product-image-box {
    width: 100%;
    height: 90%;
  }
  .product-title-box-container,
  .product-discription-box-container,
  .product-price-box-container {
    width: 90%;
    height: auto;
    padding:  0px 10px;
    
  }
  .product-title-box,
  .product-discription-box,
  .product-price-box {
    width: 100%;
   height: 30px;
    display: flex;
    font-weight: 600;
    display: flex;
    text-align: justify;
  }
  .name-show-details{
    margin-right: 5px ;
    font-weight: 700;
    color:rgb(255, 68, 0);
  }
  
  
  
  .product-price-button-container {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .edit-button-box {
    width: 100px;
    padding: 10px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: rgb(238, 0, 255);
    color: white;
    margin: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    cursor: pointer;
  }
  
  
  .discription-list{
    width:100px;
    height: 20px;
    overflow: hidden;
    color:rgb(63, 42, 53);
  }