.main-home-product-lisining {
  width: 100%;
  height: 91.5vh;
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-home-product-box {
  width: 700px;
  height: 100%;
  background-color: rgb(255, 255, 255);
  overflow-y: scroll;
  overflow-x: hidden;
  border-left: 1px solid rgb(206, 206, 206);
  border-right: 1px solid rgb(206, 206, 206);
}
.main-home-product {
  width: 100%;
  height: auto;
}
.product-box-image-container {
  width: 90%;
  height: 300px;
  background-color: rgb(215, 215, 215);
  margin-top: 30px;
  margin-left: 42px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-container {
  position: fixed;
  bottom: 0px;
  width: 700px;
  height: 80px;
  display: flex;
  align-items: center;
  background-color: rgb(249, 249, 249);
  border-top: 1px solid rgb(236, 236, 236);
}
.button-buy-and-addcart {
  padding: 15px;
  width: 250px;
  margin-left: 70px;
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid rgb(210, 0, 151);
  background-color: rgb(220, 20, 183);
  color: white;
  font-weight: 800;
  font-size: 15px;
}

.product-box-details {
  width: 100%;
  height: auto;
  margin-bottom: 100px;
}

.product-details-name {
  margin-top: 30px;
  height: 20px;
  width: 90%;
  display: flex;
  align-items: center;
  margin-left: 40px;
}
.product-details-discription {
  height: auto;
  width: 90%;
  margin-left: 40px;
}
.discription-details {
  width: 100px;
  height: auto;
}
.product-details-price {
  width: 100px;
  margin-left: 40px;
  height: 20px;
  display: flex;
  align-items: center;
}
.productkey {
  font-size: 16px;
  font-weight: 700;
}
