.mainpassword-show-box-container {
  width: 81%;
  height: 100vh;
  background: linear-gradient(
    30deg,
    rgb(43, 26, 22),
    chocolate,
    rgb(29, 202, 29),
    rgb(139, 193, 13),
    rgb(37, 2, 67)
  );
  display: flex;
  align-items: center;
  justify-content: center;
}

.mainpassword-show-message-container {
  width: 40%;
  height: 60%;
  background: linear-gradient(
    300deg,
    rgb(43, 26, 22),
    chocolate,
    rgb(139, 193, 13),
    rgb(37, 2, 67)
  );
  border-radius: 20px;
}

.heading-box-con {
  margin-top: 10px;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border-bottom: 1px solid rgb(201, 201, 201);
}
.inputvalue-box-password {
  padding: 15px;
  width: 89%;
  margin-left: 11px;
  margin-top: 10px;
  border-radius: 10px;
  border: 1px solid rgb(0, 119, 255);
  outline-color: chocolate;
 
  font-size: 17px;
}
.submitbox-container {
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#inputvalue-box-button {
  padding: 15px;
  width: 150px;
  border-radius: 10px;
  border: none;
  outline: none;
  cursor: pointer;
  background: linear-gradient(
    300deg,
    rgb(43, 26, 22),
    chocolate,
    rgb(139, 193, 13),
    red
  );
  color: white;
  font-weight: 700;
}

#emailid-changepassword{
    color:rgb(116, 116, 116) ;
 
}