.mainmessage-show-box-container{
    width:81%;
    height: 100vh;
    background: linear-gradient(
        30deg,
        rgb(43, 26, 22),
        chocolate,
        rgb(29, 202, 29),
        rgb(139, 193, 13),
        rgb(37, 2, 67)
      );
      display: flex;
      align-items: center;
      justify-content: center;
}

.mainmessage-show-message-container{
    width: 95%;
    height: 90%;
    background: linear-gradient(
      300deg,
      rgb(43, 26, 22),
      chocolate,
      rgb(139, 193, 13),
      rgb(37, 2, 67)
    );
    border-radius: 20px;
}
