.main-userprofiles-container {
  width: 81%;
  height: auto;
  background: linear-gradient(
    30deg,
    rgb(43, 26, 22),
    chocolate,
    rgb(29, 202, 29),
    rgb(139, 193, 13),
    rgb(37, 2, 67)
  );
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-userprofile-boxs{
  margin-top: 20px;
  margin-bottom: 20px;
  width: 95%;
  height: 90%;
  background: linear-gradient(
    300deg,
    rgb(43, 26, 22),
    chocolate,
    rgb(139, 193, 13),
    rgb(37, 2, 67)
  );
  border-radius: 20px;
}
.main-userprofile-heading-boxs {
  margin-top: 10px;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(205, 205, 205);
}
.main-userprofile-heading-text {
  color: rgb(255, 255, 255);
}
.main-userprofile-cons {
  width: 100%;
  height:59px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(205, 205, 205);
}
.main-userprofile-heading-text-name {
    font-size: 30px;
    font-weight: 700;
    margin-left: 20px;
  color: rgb(255, 0, 0);

}
.value-name-data {
  color: rgb(255, 255, 255);
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  margin-left: 10px;
   
}
.main-userprofile-button{
    width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}
 
.userprofile-link{
    width: 150px;
    padding: 12px;
    border-radius: 10px;
    background-color: rgb(255, 0, 0);
    text-decoration: none;
    color: white;
    border: 1px dotted white;
    display: flex;
    align-items: center;
    justify-content: center;
}